import { Component, OnInit } from '@angular/core';
import { Properties } from '../../../properties/properties';
import {environment} from "../../../../environments/environment";

@Component({
  selector: 'app-ppless',
  templateUrl: './ppless.page.html',
  styleUrls: ['./ppless.page.scss'],
})
export class PplessPage implements OnInit {
  url = environment.urlPpless;

  constructor(public properties: Properties) { }

  ngOnInit() {
    window.open(this.url , '_system', 'location=no, toolbar=no');
  }

}
